'use client';

import { useState, useEffect, useRef } from 'react';
import Image from 'next/image';

export default function ContactoPage() {
  const [isScrolled, setIsScrolled] = useState(false);
  const [visibleElements, setVisibleElements] = useState(new Set());
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  const [formData, setFormData] = useState({
    nombre: '',
    apellido: '',
    email: '',
    mensaje: ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitStatus, setSubmitStatus] = useState('');
  const parallaxRef = useRef(null);

  useEffect(() => {
    const handleScroll = () => {
      const scrollY = window.scrollY;
      setIsScrolled(scrollY > 50);
      
      // Parallax effect
      if (parallaxRef.current) {
        parallaxRef.current.style.transform = `translateY(${scrollY * 0.3}px)`;
      }
    };

    const handleMouseMove = (e) => {
      setMousePosition({ x: e.clientX, y: e.clientY });
    };

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setVisibleElements(prev => new Set([...prev, entry.target.id]));
          }
        });
      },
      { threshold: 0.1, rootMargin: '0px 0px -100px 0px' }
    );

    const elements = document.querySelectorAll('[data-animate]');
    elements.forEach(el => observer.observe(el));

    window.addEventListener('scroll', handleScroll);
    window.addEventListener('mousemove', handleMouseMove);
    
    return () => {
      window.removeEventListener('scroll', handleScroll);
      window.removeEventListener('mousemove', handleMouseMove);
      observer.disconnect();
    };
  }, []);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    // Simular envío de formulario
    try {
      await new Promise(resolve => setTimeout(resolve, 2000));
      setSubmitStatus('success');
      setFormData({ nombre: '', apellido: '', email: '', mensaje: '' });
    } catch (error) {
      setSubmitStatus('error');
    } finally {
      setIsSubmitting(false);
      setTimeout(() => setSubmitStatus(''), 5000);
    }
  };

  const contactInfo = [
    {
      title: 'Teléfono Principal',
      value: '+52 55 1234 5678',
      icon: '/icons/phone-contact.svg',
      link: 'tel:+525512345678'
    },
    {
      title: 'Email Corporativo',
      value: 'contacto@keymatch.mx',
      icon: '/icons/email-contact.svg',
      link: 'mailto:contacto@keymatch.mx'
    },
    {
      title: 'WhatsApp Business',
      value: '+52 55 9876 5432',
      icon: '/icons/whatsapp.svg',
      link: 'https://wa.me/525598765432'
    }
  ];

  const offices = [
    {
      city: 'Ciudad de México',
      address: 'Av. Reforma 250, Piso 15\nCol. Juárez, CDMX 06600',
      phone: '+52 55 1234 5678',
      image: '/images/office-cdmx.jpg'
    },
    {
      city: 'Guadalajara',
      address: 'Av. López Mateos 2077\nCol. Italiana, Guadalajara 44648',
      phone: '+52 33 8765 4321',
      image: '/images/office-gdl.jpg'
    },
    {
      city: 'Monterrey',
      address: 'Ave. Constitución 1000\nCol. Centro, Monterrey 64000',
      phone: '+52 81 5555 1234',
      image: '/images/office-mty.jpg'
    }
  ];

  const faqs = [
    {
      question: '¿Cuánto tiempo toma encontrar candidatos?',
      answer: 'Nuestro tiempo promedio es de 2-3 semanas para presentar los primeros candidatos calificados, y 4-6 semanas para completar el proceso de contratación.'
    },
    {
      question: '¿Cuáles son sus tarifas?',
      answer: 'Nuestras tarifas son competitivas y basadas en el nivel del puesto. Ofrecemos una consulta gratuita donde discutimos presupuestos y expectativas.'
    },
    {
      question: '¿Trabajan con startups pequeñas?',
      answer: 'Sí, trabajamos con empresas de todos los tamaños, desde startups hasta corporaciones. Adaptamos nuestro servicio a las necesidades específicas de cada cliente.'
    },
    {
      question: '¿Qué garantías ofrecen?',
      answer: 'Ofrecemos 90 días de garantía. Si el candidato no cumple expectativas en este período, lo reemplazamos sin costo adicional.'
    }
  ];

  return (
    <div className="min-h-screen bg-white">
      <style jsx global>{`
        * {
          margin: 0;
          padding: 0;
          box-sizing: border-box;
        }

        body {
          font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
          line-height: 1.6;
          color: #1a202c;
          overflow-x: hidden;
        }

        .container {
          max-width: 1200px;
          margin: 0 auto;
          padding: 0 2rem;
        }

        .section-spacing {
          padding: 6rem 0;
        }

        .section-spacing-large {
          padding: 8rem 0;
        }

        /* Animaciones */
        @keyframes fadeInUp {
          from {
            opacity: 0;
            transform: translateY(60px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }

        @keyframes slideInLeft {
          from {
            opacity: 0;
            transform: translateX(-80px);
          }
          to {
            opacity: 1;
            transform: translateX(0);
          }
        }

        @keyframes slideInRight {
          from {
            opacity: 0;
            transform: translateX(80px);
          }
          to {
            opacity: 1;
            transform: translateX(0);
          }
        }

        @keyframes float {
          0%, 100% { transform: translateY(0px); }
          50% { transform: translateY(-12px); }
        }

        @keyframes pulse {
          0%, 100% { transform: scale(1); }
          50% { transform: scale(1.05); }
        }

        @keyframes slideInFromBottom {
          from {
            opacity: 0;
            transform: translateY(100px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }

        @keyframes scaleIn {
          from {
            opacity: 0;
            transform: scale(0.8);
          }
          to {
            opacity: 1;
            transform: scale(1);
          }
        }

        @keyframes spin {
          from { transform: rotate(0deg); }
          to { transform: rotate(360deg); }
        }

        .animate-fade-up {
          animation: fadeInUp 0.8s ease-out forwards;
        }

        .animate-slide-left {
          animation: slideInLeft 0.8s ease-out forwards;
        }

        .animate-slide-right {
          animation: slideInRight 0.8s ease-out forwards;
        }

        .animate-slide-bottom {
          animation: slideInFromBottom 0.8s ease-out forwards;
        }

        .animate-scale-in {
          animation: scaleIn 0.6s ease-out forwards;
        }

        .animate-float {
          animation: float 3s ease-in-out infinite;
        }

        .animate-pulse {
          animation: pulse 2s ease-in-out infinite;
        }

        .animate-spin {
          animation: spin 1s linear infinite;
        }

        .hover-lift {
          transition: all 0.3s ease;
        }

        .hover-lift:hover {
          transform: translateY(-5px);
        }

        .hover-scale {
          transition: all 0.3s ease;
        }

        .hover-scale:hover {
          transform: scale(1.02);
        }

        .hover-glow {
          transition: all 0.3s ease;
        }

        .hover-glow:hover {
          box-shadow: 0 20px 60px rgba(59, 130, 246, 0.3);
        }

        .gradient-text {
          background: linear-gradient(135deg, #1e293b 0%, #3b82f6 50%, #1d4ed8 100%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          background-clip: text;
        }

        .gradient-bg {
          background: linear-gradient(135deg, #3b82f6, #1d4ed8);
        }

        .gradient-green {
          background: linear-gradient(135deg, #059669, #047857);
        }

        .glass-effect {
          background: rgba(255, 255, 255, 0.95);
          backdrop-filter: blur(20px);
          border-bottom: 1px solid rgba(229, 231, 235, 0.3);
        }

        .card-shadow {
          box-shadow: 0 10px 40px rgba(0, 0, 0, 0.08);
        }

        .card-shadow-hover {
          transition: all 0.4s ease;
        }

        .card-shadow-hover:hover {
          transform: translateY(-8px);
          box-shadow: 0 20px 60px rgba(0, 0, 0, 0.12);
        }

        .btn-primary {
          background: linear-gradient(135deg, #3b82f6, #1d4ed8);
          color: white;
          padding: 1rem 2rem;
          border-radius: 50px;
          font-weight: 600;
          font-size: 1rem;
          border: none;
          cursor: pointer;
          transition: all 0.3s ease;
          box-shadow: 0 4px 20px rgba(59, 130, 246, 0.3);
          text-decoration: none;
          display: inline-block;
          text-align: center;
        }

        .btn-primary:hover {
          transform: translateY(-3px);
          box-shadow: 0 8px 30px rgba(59, 130, 246, 0.4);
        }

        .btn-primary:disabled {
          opacity: 0.6;
          cursor: not-allowed;
          transform: none;
        }

        .btn-secondary {
          background: transparent;
          color: #3b82f6;
          padding: 1rem 2rem;
          border: 2px solid #3b82f6;
          border-radius: 50px;
          font-weight: 600;
          font-size: 1rem;
          cursor: pointer;
          transition: all 0.3s ease;
          text-decoration: none;
          display: inline-block;
          text-align: center;
        }

        .btn-secondary:hover {
          background: #3b82f6;
          color: white;
          transform: translateY(-3px);
        }

        .hero-bg {
          background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 50%, #f1f5f9 100%);
          min-height: 100vh;
          display: flex;
          align-items: center;
          position: relative;
          overflow: hidden;
        }

        .hero-bg::before {
          content: '';
          position: absolute;
          top: -50%;
          right: -50%;
          width: 100%;
          height: 200%;
          background: radial-gradient(circle, rgba(59, 130, 246, 0.05) 0%, transparent 50%);
          animation: float 8s ease-in-out infinite;
        }

        .particles {
          position: absolute;
          width: 100%;
          height: 100%;
          overflow: hidden;
          pointer-events: none;
        }

        .particle {
          position: absolute;
          background: rgba(59, 130, 246, 0.1);
          border-radius: 50%;
          animation: float 6s ease-in-out infinite;
        }

        .particle:nth-child(1) {
          width: 80px;
          height: 80px;
          top: 20%;
          left: 10%;
          animation-delay: 0s;
        }

        .particle:nth-child(2) {
          width: 60px;
          height: 60px;
          top: 60%;
          right: 10%;
          animation-delay: 2s;
        }

        .particle:nth-child(3) {
          width: 40px;
          height: 40px;
          bottom: 20%;
          left: 20%;
          animation-delay: 4s;
        }

        .grid-2 {
          display: grid;
          grid-template-columns: 1fr 1fr;
          gap: 4rem;
          align-items: center;
        }

        .grid-3 {
          display: grid;
          grid-template-columns: repeat(3, 1fr);
          gap: 2rem;
        }

        .contact-card {
          background: white;
          border-radius: 1.5rem;
          padding: 2rem;
          text-align: center;
          box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
          transition: all 0.3s ease;
          border: 1px solid #e2e8f0;
        }

        .contact-card:hover {
          transform: translateY(-5px);
          box-shadow: 0 15px 40px rgba(0, 0, 0, 0.12);
          border-color: #3b82f6;
        }

        .office-card {
          background: white;
          border-radius: 2rem;
          overflow: hidden;
          box-shadow: 0 10px 40px rgba(0, 0, 0, 0.08);
          transition: all 0.3s ease;
        }

        .office-card:hover {
          transform: translateY(-8px);
          box-shadow: 0 20px 60px rgba(0, 0, 0, 0.15);
        }

        .form-group {
          margin-bottom: 1.5rem;
        }

        .form-label {
          display: block;
          font-weight: 600;
          color: #374151;
          margin-bottom: 0.5rem;
          font-size: 0.9rem;
        }

        .form-input {
          width: 100%;
          padding: 1rem 1.5rem;
          border: 2px solid #e2e8f0;
          border-radius: 1rem;
          font-size: 1rem;
          transition: all 0.3s ease;
          background: white;
        }

        .form-input:focus {
          outline: none;
          border-color: #3b82f6;
          box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
        }

        .form-textarea {
          width: 100%;
          padding: 1rem 1.5rem;
          border: 2px solid #e2e8f0;
          border-radius: 1rem;
          font-size: 1rem;
          min-height: 120px;
          resize: vertical;
          transition: all 0.3s ease;
          background: white;
          font-family: inherit;
        }

        .form-textarea:focus {
          outline: none;
          border-color: #3b82f6;
          box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
        }

        .success-message {
          background: linear-gradient(135deg, #059669, #047857);
          color: white;
          padding: 1rem 1.5rem;
          border-radius: 1rem;
          margin-bottom: 1rem;
          text-align: center;
          font-weight: 600;
        }

        .error-message {
          background: linear-gradient(135deg, #dc2626, #b91c1c);
          color: white;
          padding: 1rem 1.5rem;
          border-radius: 1rem;
          margin-bottom: 1rem;
          text-align: center;
          font-weight: 600;
        }

        .faq-item {
          background: white;
          border-radius: 1rem;
          padding: 1.5rem;
          margin-bottom: 1rem;
          box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
          border: 1px solid #e2e8f0;
          cursor: pointer;
          transition: all 0.3s ease;
        }

        .faq-item:hover {
          border-color: #3b82f6;
          box-shadow: 0 8px 25px rgba(59, 130, 246, 0.1);
        }

        .faq-question {
          font-weight: 600;
          color: #1e293b;
          margin-bottom: 0.5rem;
        }

        .faq-answer {
          color: #64748b;
          line-height: 1.6;
        }

        /* Utility classes */
        .text-center { text-align: center; }
        .mb-4 { margin-bottom: 1rem; }
        .mb-6 { margin-bottom: 1.5rem; }
        .mb-8 { margin-bottom: 2rem; }
        .mb-12 { margin-bottom: 3rem; }
        .mb-16 { margin-bottom: 4rem; }
        .mb-20 { margin-bottom: 5rem; }
        .mt-8 { margin-top: 2rem; }
        .mt-12 { margin-top: 3rem; }
        .p-6 { padding: 1.5rem; }
        .p-8 { padding: 2rem; }
        .px-4 { padding-left: 1rem; padding-right: 1rem; }
        .px-6 { padding-left: 1.5rem; padding-right: 1.5rem; }
        .py-2 { padding-top: 0.5rem; padding-bottom: 0.5rem; }
        .py-4 { padding-top: 1rem; padding-bottom: 1rem; }
        .rounded-full { border-radius: 9999px; }
        .rounded-xl { border-radius: 0.75rem; }
        .rounded-2xl { border-radius: 1rem; }
        .rounded-3xl { border-radius: 1.5rem; }
        .flex { display: flex; }
        .flex-col { flex-direction: column; }
        .items-center { align-items: center; }
        .justify-center { justify-content: center; }
        .justify-between { justify-content: space-between; }
        .gap-2 { gap: 0.5rem; }
        .gap-4 { gap: 1rem; }
        .gap-6 { gap: 1.5rem; }
        .gap-8 { gap: 2rem; }
        .space-y-4 > * + * { margin-top: 1rem; }
        .space-y-6 > * + * { margin-top: 1.5rem; }
        .text-sm { font-size: 0.875rem; }
        .text-base { font-size: 1rem; }
        .text-lg { font-size: 1.125rem; }
        .text-xl { font-size: 1.25rem; }
        .text-2xl { font-size: 1.5rem; }
        .text-3xl { font-size: 1.875rem; }
        .text-4xl { font-size: 2.25rem; }
        .text-5xl { font-size: 3rem; }
        .font-medium { font-weight: 500; }
        .font-semibold { font-weight: 600; }
        .font-bold { font-weight: 700; }
        .font-black { font-weight: 900; }
        .text-slate-600 { color: #64748b; }
        .text-slate-700 { color: #374151; }
        .text-slate-900 { color: #1e293b; }
        .text-white { color: white; }
        .text-blue-600 { color: #3b82f6; }
        .text-green-600 { color: #059669; }
        .bg-white { background-color: white; }
        .bg-blue-50 { background-color: #eff6ff; }
        .bg-slate-50 { background-color: #f8fafc; }
        .bg-green-600 { background-color: #059669; }
        .bg-green-700 { background-color: #047857; }
        .hover\\:bg-green-700:hover { background-color: #047857; }
        .border { border-width: 1px; }
        .border-slate-200 { border-color: #e2e8f0; }
        .border-blue-200 { border-color: #bfdbfe; }
        .w-6 { width: 1.5rem; }
        .w-8 { width: 2rem; }
        .w-12 { width: 3rem; }
        .w-16 { width: 4rem; }
        .w-24 { width: 6rem; }
        .h-6 { height: 1.5rem; }
        .h-8 { height: 2rem; }
        .h-12 { height: 3rem; }
        .h-16 { height: 4rem; }
        .h-24 { height: 6rem; }
        .h-32 { height: 8rem; }
        .h-48 { height: 12rem; }
        .h-64 { height: 16rem; }
        .w-full { width: 100%; }
        .h-full { height: 100%; }
        .fixed { position: fixed; }
        .relative { position: relative; }
        .absolute { position: absolute; }
        .top-0 { top: 0; }
        .inset-0 { top: 0; right: 0; bottom: 0; left: 0; }
        .z-50 { z-index: 50; }
        .z-10 { z-index: 10; }
        .opacity-0 { opacity: 0; }
        .opacity-90 { opacity: 0.9; }
        .opacity-95 { opacity: 0.95; }
        .overflow-hidden { overflow: hidden; }
        .transition-all { transition: all 0.3s ease; }

        @media (max-width: 1024px) {
          .grid-2 { grid-template-columns: 1fr; gap: 3rem; }
          .text-5xl { font-size: 2.5rem; }
        }

        @media (max-width: 768px) {
          .container { padding: 0 1rem; }
          .section-spacing { padding: 4rem 0; }
          .section-spacing-large { padding: 5rem 0; }
          .grid-3 { grid-template-columns: 1fr; gap: 1.5rem; }
          .text-4xl { font-size: 1.875rem; }
          .text-5xl { font-size: 2rem; }
          .flex-mobile-col { flex-direction: column; }
          .gap-mobile-4 { gap: 1rem; }
        }
      `}</style>

      {/* Header */}
      <header className={`fixed top-0 w-full z-50 transition-all ${
        isScrolled ? 'glass-effect shadow-lg' : 'glass-effect'
      }`}>
        <div className="container">
          <div className="flex justify-between items-center py-4">
            <div className="flex items-center gap-4">
              <Image 
                src="/logo/keymatch-logo.svg" 
                alt="KeyMatch Logo" 
                width={40} 
                height={40}
                className="animate-pulse"
              />
              <div className="text-2xl font-black gradient-text">
                KeyMatch
              </div>
            </div>
            
            <nav className="hidden md:flex gap-8">
              <a href="/" className="text-slate-600 hover:text-blue-600 font-medium transition-all hover-lift">
                Inicio
              </a>
              <a href="/#servicios" className="text-slate-600 hover:text-blue-600 font-medium transition-all hover-lift">
                Servicios
              </a>
              <a href="/por-que-nosotros" className="text-slate-600 hover:text-blue-600 font-medium transition-all hover-lift">
                ¿Por qué nosotros?
              </a>
              <a href="/#testimonios" className="text-slate-600 hover:text-blue-600 font-medium transition-all hover-lift">
                Testimonios
              </a>
              <a href="/contacto" className="text-blue-600 font-medium transition-all hover-lift">
                Contacto
              </a>
            </nav>
            
            <a href="/consulta" className="gradient-bg text-white px-6 py-2 rounded-full font-semibold hover:shadow-lg transition-all hover-lift">
              Consulta Gratuita
            </a>
          </div>
        </div>
      </header>

      {/* Hero Section */}
      <section className="hero-bg">
        <div className="particles">
          <div className="particle"></div>
          <div className="particle"></div>
          <div className="particle"></div>
        </div>
        
        <div className="container">
          <div className="grid-2">
            <div 
              className={`${visibleElements.has('hero-content') ? 'animate-slide-left' : 'opacity-0'}`}
              data-animate
              id="hero-content"
            >
              <div className="flex items-center gap-2 bg-blue-50 text-blue-600 px-4 py-2 rounded-full text-sm font-semibold mb-8 border border-blue-200 w-max">
                <span className="w-2 h-2 bg-blue-600 rounded-full animate-pulse"></span>
                Conecta con Nosotros
              </div>
              
              <h1 className="text-5xl font-black mb-8 gradient-text">
                Comencemos una Conversación
              </h1>
              
              <p className="text-xl text-slate-600 mb-12 leading-relaxed">
                Estamos aquí para ayudarte a encontrar el talento perfecto para tu empresa. 
                Contáctanos hoy y descubre cómo podemos transformar tu equipo con los mejores 
                profesionales de México.
              </p>
              
              <div className="space-y-6">
                <div className="flex items-center gap-4">
                  <div className="w-12 h-12 bg-gradient-to-br from-blue-500 to-blue-600 rounded-full flex items-center justify-center">
                    <Image src="/icons/phone-white.svg" alt="Teléfono" width={20} height={20} />
                  </div>
                  <div>
                    <p className="font-semibold text-slate-900">Llámanos Directamente</p>
                    <a href="tel:+525512345678" className="text-blue-600 font-bold text-lg hover:text-blue-700">
                      +52 55 1234 5678
                    </a>
                  </div>
                </div>
                
                <div className="flex items-center gap-4">
                  <div className="w-12 h-12 bg-gradient-to-br from-green-500 to-green-600 rounded-full flex items-center justify-center">
                    <Image src="/icons/email-white.svg" alt="Email" width={20} height={20} />
                  </div>
                  <div>
                    <p className="font-semibold text-slate-900">Envíanos un Email</p>
                    <a href="mailto:contacto@keymatch.mx" className="text-green-600 font-bold text-lg hover:text-green-700">
                      contacto@keymatch.mx
                    </a>
                  </div>
                </div>
              </div>
            </div>
            
            <div 
              className={`${visibleElements.has('hero-form') ? 'animate-slide-right' : 'opacity-0'}`}
              data-animate
              id="hero-form"
            >
              <div className="bg-white p-8 rounded-3xl card-shadow">
                <h3 className="text-2xl font-bold text-slate-900 mb-6 text-center">
                  Escríbenos un Mensaje
                </h3>
                
                {submitStatus === 'success' && (
                  <div className="success-message">
                    ¡Mensaje enviado exitosamente! Te contactaremos pronto.
                  </div>
                )}
                
                {submitStatus === 'error' && (
                  <div className="error-message">
                    Error al enviar el mensaje. Por favor intenta de nuevo.
                  </div>
                )}
                
                <form onSubmit={handleSubmit}>
                  <div className="grid-2">
                    <div className="form-group">
                      <label className="form-label">Nombre *</label>
                      <input
                        type="text"
                        name="nombre"
                        value={formData.nombre}
                        onChange={handleInputChange}
                        className="form-input"
                        required
                        placeholder="Tu nombre"
                      />
                    </div>
                    
                    <div className="form-group">
                      <label className="form-label">Apellido *</label>
                      <input
                        type="text"
                        name="apellido"
                        value={formData.apellido}
                        onChange={handleInputChange}
                        className="form-input"
                        required
                        placeholder="Tu apellido"
                      />
                    </div>
                  </div>
                  
                  <div className="form-group">
                    <label className="form-label">Email de Contacto *</label>
                    <input
                      type="email"
                      name="email"
                      value={formData.email}
                      onChange={handleInputChange}
                      className="form-input"
                      required
                      placeholder="tu@email.com"
                    />
                  </div>
                  
                  <div className="form-group">
                    <label className="form-label">Mensaje *</label>
                    <textarea
                      name="mensaje"
                      value={formData.mensaje}
                      onChange={handleInputChange}
                      className="form-textarea"
                      required
                      placeholder="Cuéntanos sobre tu empresa y qué tipo de talento necesitas..."
                    />
                  </div>
                  
                  <button 
                    type="submit" 
                    className="btn-primary w-full flex items-center justify-center gap-2"
                    disabled={isSubmitting}
                  >
                    {isSubmitting ? (
                      <>
                        <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                        Enviando...
                      </>
                    ) : (
                      'Enviar Mensaje'
                    )}
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Contact Info */}
      <section className="section-spacing bg-white">
        <div className="container">
          <div 
            className={`text-center mb-16 ${visibleElements.has('contact-header') ? 'animate-fade-up' : 'opacity-0'}`}
            data-animate
            id="contact-header"
          >
            <h2 className="text-4xl font-black text-slate-900 mb-6">
              Múltiples Formas de Contactarnos
            </h2>
            <p className="text-lg text-slate-600 max-w-3xl mx-auto">
              Elige la forma que más te convenga para iniciar una conversación con nuestro equipo.
            </p>
          </div>
          
          <div className="grid-3">
            {contactInfo.map((info, index) => (
              <div 
                key={index}
                className={`contact-card ${
                  visibleElements.has(`contact-${index}`) ? 'animate-scale-in' : 'opacity-0'
                }`}
                data-animate
                id={`contact-${index}`}
                style={{ animationDelay: `${index * 0.2}s` }}
              >
                <Image 
                  src={info.icon} 
                  alt={info.title} 
                  width={48} 
                  height={48}
                  className="mx-auto mb-4 animate-pulse"
                />
                <h3 className="text-xl font-bold text-slate-900 mb-3">
                  {info.title}
                </h3>
                <a 
                  href={info.link}
                  className="text-blue-600 font-bold text-lg hover:text-blue-700 transition-colors"
                >
                  {info.value}
                </a>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Offices */}
      <section className="section-spacing bg-slate-50">
        <div className="container">
          <div 
            className={`text-center mb-16 ${visibleElements.has('offices-header') ? 'animate-fade-up' : 'opacity-0'}`}
            data-animate
            id="offices-header"
          >
            <h2 className="text-4xl font-black text-slate-900 mb-6">
              Nuestras Oficinas en México
            </h2>
            <p className="text-lg text-slate-600 max-w-3xl mx-auto">
              Presencia nacional con oficinas estratégicamente ubicadas para servir mejor a nuestros clientes.
            </p>
          </div>
          
          <div className="grid-3">
            {offices.map((office, index) => (
              <div 
                key={index}
                className={`office-card ${
                  visibleElements.has(`office-${index}`) ? 'animate-slide-bottom' : 'opacity-0'
                }`}
                data-animate
                id={`office-${index}`}
                style={{ animationDelay: `${index * 0.2}s` }}
              >
                <div className="relative h-48">
                  <Image
                    src={office.image}
                    alt={`Oficina ${office.city}`}
                    fill
                    className="object-cover"
                  />
                </div>
                <div className="p-6">
                  <h3 className="text-xl font-bold text-slate-900 mb-3">
                    {office.city}
                  </h3>
                  <p className="text-slate-600 mb-4 whitespace-pre-line">
                    {office.address}
                  </p>
                  <a 
                    href={`tel:${office.phone.replace(/\s/g, '')}`}
                    className="text-blue-600 font-semibold hover:text-blue-700 transition-colors"
                  >
                    {office.phone}
                  </a>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* FAQ */}
      <section className="section-spacing bg-white">
        <div className="container">
          <div 
            className={`text-center mb-16 ${visibleElements.has('faq-header') ? 'animate-fade-up' : 'opacity-0'}`}
            data-animate
            id="faq-header"
          >
            <h2 className="text-4xl font-black text-slate-900 mb-6">
              Preguntas Frecuentes
            </h2>
            <p className="text-lg text-slate-600 max-w-3xl mx-auto">
              Encuentra respuestas rápidas a las consultas más comunes sobre nuestros servicios.
            </p>
          </div>
          
          <div className="max-w-4xl mx-auto">
            {faqs.map((faq, index) => (
              <div 
                key={index}
                className={`faq-item ${
                  visibleElements.has(`faq-${index}`) ? 'animate-fade-up' : 'opacity-0'
                }`}
                data-animate
                id={`faq-${index}`}
                style={{ animationDelay: `${index * 0.1}s` }}
              >
                <h4 className="faq-question">{faq.question}</h4>
                <p className="faq-answer">{faq.answer}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="section-spacing gradient-bg text-white relative overflow-hidden">
        <div className="absolute inset-0 bg-gradient-to-r from-blue-600/20 to-blue-800/20"></div>
        <div className="container relative z-10">
          <div 
            className={`text-center ${visibleElements.has('cta-content') ? 'animate-fade-up' : 'opacity-0'}`}
            data-animate
            id="cta-content"
          >
            <h2 className="text-4xl font-black mb-6">
              ¿Listo para Encontrar tu Próximo Gran Talento?
            </h2>
            <p className="text-xl mb-12 opacity-95 max-w-3xl mx-auto">
              No esperes más. Contáctanos hoy y comienza a construir el equipo 
              que llevará tu empresa al siguiente nivel.
            </p>
            
            <div className="flex flex-mobile-col gap-6 gap-mobile-4 justify-center">
              <a href="/consulta" className="bg-white text-blue-600 px-8 py-4 rounded-full font-bold text-lg hover:shadow-2xl transition-all hover-lift">
                Consulta Gratuita
              </a>
              <a href="tel:+525512345678" className="border-2 border-white text-white px-8 py-4 rounded-full font-bold text-lg hover:bg-white hover:text-blue-600 transition-all hover-lift">
                Llamar Ahora
              </a>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}